import React, { useEffect, useState } from "react";
import { useParams, useHistory } from "react-router";
import Zutat from "../Zutat/Zutat";

const Recipe = () => {
    let { id } = useParams();
    let history = useHistory();
    const [rezept, setRezept] = useState({});
    const [zutaten, setZutaten] = useState([]);
    const [edit, setEdit] = useState(false);

    const [titel, setTitel] = useState("");
    const [beschreibung, setBeschreibung] = useState("");

    const [menge, setMenge] = useState("");
    const [einheit, setEinheit] = useState("");
    const [bezeichnung, setBezeichnung] = useState("");

    const updateRezept = () => {
        setEdit(!edit);

        const url = "http://localhost:8080/rezepte/" + id;

        const body = {
            titel: titel,
            beschreibung: beschreibung,
            zutaten: zutaten,
        };

        fetch(url, {
            headers: {
                "content-type": "application/json",
            },
            method: "PUT",
            body: JSON.stringify(body),
        })
            .then((response) => response.json())
            .then((data) => {
                console.log(data);
            })
            .catch((error) => console.error(error));
    };

    const deleteRezept = () => {
        const url = "http://localhost:8080/rezepte/" + id;

        fetch(url, {
            headers: {
                "content-type": "application/json",
            },
            method: "DELETE",
        }).catch((error) => console.error(error));

        history.push("/");
    };

    const zutatEntfernen = (rId) => {
        const neueZutaten = zutaten.filter((zutat) => {
            return zutat.id !== rId;
        });

        setZutaten(neueZutaten);

        rezept.zutaten = neueZutaten;
        setRezept(rezept);
    };

    const zutatHinzu = (zutat) => {
        setZutaten([...zutaten, zutat]);
    };

    useEffect(() => {
        const url = "http://localhost:8080/rezepte/" + id;

        fetch(url, {
            headers: {
                "content-type": "application/json",
            },
            method: "GET",
        })
            .then((response) => response.json())
            .then((data) => {
                setRezept(data);
                setZutaten(
                    data.zutaten.map((zutat) => {
                        return {
                            id: zutat.zutat.id,
                            name: zutat.zutat.name,
                            menge: zutat.menge,
                            einheit: zutat.einheit,
                        };
                    })
                );

                setTitel(data.titel);
                setBeschreibung(data.beschreibung);
            })
            .catch((error) => console.error(error));
    }, [id]);

    return (
        <>
            <p style={{ fontSize: 30, fontWeight: "bold" }} onClick={() => history.push("/")}>
                Rezept Tracker
            </p>

            {!edit ? (
                <div className="rezept">
                    <h1>{titel}</h1>
                    <p>{beschreibung}</p>

                    <div className="zutaten">
                        {zutaten.map((zutat, index) => (
                            <p className="zutat" key={index}>
                                {zutat.menge} {zutat.einheit} {zutat.name}
                            </p>
                        ))}
                    </div>
                </div>
            ) : (
                <div className="rezept-edit">
                    <div className="text-bearbeiten">
                        <input
                            className="titel-input"
                            value={titel}
                            onChange={(e) => setTitel(e.target.value)}
                        />
                        <textarea
                            className="beschreibung-input"
                            value={beschreibung}
                            onChange={(e) => setBeschreibung(e.target.value)}
                        />
                    </div>

                    <div className="zutaten-bearbeiten">
                        {zutaten.map((zutat, index) => (
                            <div className="zutat-bearbeiten">
                                <Zutat
                                    key={index}
                                    id={zutat.id}
                                    name={zutat.name}
                                    menge={zutat.menge}
                                    einheit={zutat.einheit}
                                    editing={true}
                                    entfernen={zutatEntfernen}
                                />
                            </div>
                        ))}

                        <div>
                            <input placeholder="Menge" onChange={(e) => setMenge(e.target.value)} />
                            <input
                                placeholder="Einheit"
                                onChange={(e) => setEinheit(e.target.value)}
                            />
                            <input
                                placeholder="Bezeichnung"
                                onChange={(e) => setBezeichnung(e.target.value)}
                            />
                            <button
                                onClick={() =>
                                    zutatHinzu({
                                        name: bezeichnung,
                                        menge: menge,
                                        einheit: einheit,
                                    })
                                }
                            >
                                Hinzuf√ºgen
                            </button>
                        </div>
                    </div>
                </div>
            )}
            <div className="interactions">
                <p onClick={() => updateRezept()}>{!edit ? "Bearbeiten" : "Speichern"}</p>
                <p onClick={() => deleteRezept()}>L√∂schen</p>
            </div>
        </>
    );
};

export default Recipe;
