import { useHistory } from "react-router";

const RecipePreview = ({ id, titel, beschreibung }) => {
    let history = useHistory();

    return (
        <div
            className="preview"
            onClick={() => {
                history.push(`/${id}`);
            }}
        >
            <h2>{titel}</h2>
            <p>{beschreibung}</p>
        </div>
    );
};

export default RecipePreview;
