import React from "react";

const Zutat = ({ id, menge, einheit, name, editing, entfernen }) => {
    return (
        <>
            {editing ? (
                <>
                    <p className="zutat">
                        {menge} {einheit} {name}
                    </p>
                    <button className="entfernen" onClick={() => entfernen(id)}>
                        Entfernen
                    </button>
                </>
            ) : (
                <>
                    <p className="zutat">
                        {menge} {einheit} {name}
                    </p>
                </>
            )}
        </>
    );
};

export default Zutat;
